// 路由规则模块
import Home from "@/views/Home";
import Blog from "@/views/Blog";
import About from "@/views/About";
import Project from "@/views/Project";
import Message from "@/views/Message";
// 博客详情页
import DetailBlog from "@/views/Blog/main.vue";
// 404 页面
import NotFound from "@/views/NotFound";

import "nprogress/nprogress.css";
import {start,done} from "nprogress";
// import { resolve } from "core-js/fn/promise";

// 模拟一下延迟加载
/**
 * 模拟延迟加载数据，这个函数什么都不做，就阻止页面加载
 * @param {Number} time 默认值为 2000
 * @returns 
 */
function delay(time = 2000) {
    return new Promise((resolve) => {
        setTimeout(() =>{
            resolve();
        },time);
    });
}

/**
 * 获取数据
 */
function getAsyncData(pageCompResolver) {
    return async () => {
        start();
        // 判断是否是开发环境
        if (process.env.NODE_ENV === 'development') {
            await delay(2000);
        }
        const comp = await pageCompResolver();
        done();
        return comp;
    }
}

export default [{
        name: "Home",
        path: '/',
        component: getAsyncData(() => import("@/views/Home")),
        meta: {
            title: "首页",
        }
    },
    {
        name: "Blog",
        path: '/blog',
        component: getAsyncData(() => import("@/views/Blog")),
        meta: {
            title: "文章"
        }
    },
    {
        // 分类博客
        name: "ClassifyBlog",
        path: '/blog/cate/:categoryId',
        component: getAsyncData(() => import("@/views/Blog")),

    },
    {
        // 博客详情页
        name: "DetailsBlog",
        path: '/blog/:categoryId',
        component: getAsyncData(() => import("@/views/Blog/main.vue")),
        meta: {
            title: "文章详情页"
        }
    },
    {
        // 关于我
        name: "About",
        path: '/about',
        component: getAsyncData(() => import("@/views/About")),
        meta: {
            title: "关于我"
        }
    },
    {
        // 项目&效果
        name: "Project",
        path: '/project',
        component: getAsyncData(() => import("@/views/Project")),
        meta: {
            title: "项目&效果"
        }
    },
    {
        // 留言板
        name: "Message",
        path: '/message',
        component: getAsyncData(() => import("@/views/Message")),
        meta: {
            title: "留言板"
        }
    },
    {
        // 404 页面
        name: "Not Found",
        path: "*",
        component: NotFound
    }
];