// 轮播图数据仓库模块
import getBanner from "@/api/api-banner";

export default {
    namespaced: true,
    state: {
        loading: false,
        data: []
    },
    mutations: {
        // 设置状态
        setBannerStatus(state,payload) {
            state.loading = payload;
        },
        // 设置 banner 数据
        setBanner(state,payload) {
            state.data = payload;
        }
    },
    actions: {
        async getData(ctx) {
            if (ctx.state.data.length > 0) {
                return;
            }
            ctx.commit('setBannerStatus',true);
            const resp = await getBanner();
            ctx.commit('setBannerStatus',false);
            ctx.commit('setBanner',resp)
        }
    }
}