// 根据路由信息，获取文章的评论并显示
<template>
  <div class="blog-comment-container" v-loading="isLoading">
    <Message
      :listData="data.rows"
      title="评论列表"
      :total="data.total"
      @submit="handleSubmit"
    />
  </div>
</template>

<script>
// 导入留言板插件
import Message from "@/components/MessageArea";
// 导入关于 blog 的数据接口
import * as API from "@/api/api-blog.js";
// 导入获取远程数据的公共配置
import { getData } from "@/mixins";

export default {
  mixins: [getData()],
  components: {
    Message,
  },
  data() {
    return {
      page: 1,
      limit: 10,
    };
  },
  computed: {
    // 判断是否还有数据
    hasMoreData() {
      return this.data.total <= this.data.rows.length;
    },
  },
  mounted() {
    // window.getMoreData = this.getMoreData;
    this.$Bus.$on("handleScroll", this.scrollLowest);
  },
  destroyed() {
    this.$Bus.$off("handleScroll", this.scrollLowest);
  },
  methods: {
    async fatchData() {
      return await API.getComment(
        this.page,
        this.limit,
        this.$route.params.categoryId
      );
    },
    async handleSubmit(data, callBack) {
      const resp = await API.submitComment({
        blogId: this.$route.params.categoryId,
        ...data,
      });
      this.data.rows.unshift(resp);
      callBack("评论成功");
    },
    // 获取更多评论数据
    async getMoreData() {
      if (this.hasMoreData) {
        return;
      }
      this.isLoading = true;

      const resp = await this.fatchData();
      this.data.rows = this.data.rows.concat(resp.rows);
      this.isLoading = false;
    },
    // 当滚动条到最低的时候触发事件
    scrollLowest(dom) {
      if (this.isLoading) {
        return;
      }
      const range = 100;
      // 总高度：dom.scrollHeight
      // 可见高度：dom.clientHeight
      // 滚动的高度：dom.scrollTop
      const desc = dom.scrollHeight - dom.clientHeight - dom.scrollTop;
      // console.log(desc);
      if (range > desc) {
        this.getMoreData();
      }
      // this.getMoreData();
    },
  },
};
</script>

<style scoped lang="less">
.blog-comment-container {
  position: relative;
  min-width: 480px;
  .messageArea-form {
    width: 400px;
    background: red;
  }
}
</style>