// 关于我页面组件
<template>
  <div class="about-container" v-loading="loading && !isLoading">
    <iframe
      :src="data"
      class="about-content"
      @load="isLoading = false"
    ></iframe>
  </div>
</template>

<script>
// 导入加载图标 loading
import { loading } from "@/instruction";
import { mapState } from "vuex";

export default {
  data() {
    return {
      isLoading: true,
    };
  },
  // components: {
  //   loading,
  // },
  mounted() {
    this.$store.dispatch("about/getData");
  },
  computed: {
    ...mapState("about", ["loading", "data"]),
  },
};
</script>

<style scoped lang="less">
.about-container {
  position: relative;
  width: 100%;
  height: 100%;
}

.about-content {
  width: 100%;
  height: 100%;
  display: block;
  box-sizing: border-box;
}
</style>