// 仓库主模块
import Vue from "vue";
// import Vuex from "vuex";
import {Store,install} from "vuex";
// 判断是否是通过传统代码来引入 Vue
if (!window.Vuex) {
    install(Vue);
}
// Vue.use(Vuex);
import banner from "./banner";
import setting from "./setting";
import about from "./about";
import project from "./project";

const store = new Store({
    strict: true,
    modules: {
        banner,
        setting,
        about,
        project
    }
});

export default store;