// 右边列表组件
<template>
  <ul class="right-list-container">
    <li v-for="item in listData" :key="item.id" class="list-item">
      <span
        @click="selecting(item)"
        :class="{
          selecting: item.isSelecting,
        }"
        >{{ item.name }}</span
      >
      <span
        @click="selecting(item)"
        :class="{
          count: true,
          selecting: item.isSelecting,
        }"
        >{{ item.articleCount }}</span
      >
      <RightList :listData="item.children" @selecting="selecting" />
    </li>
  </ul>
</template>

<script>
export default {
  name: "RightList",
  props: {
    listData: {
      type: Array,
      //   当默认值是数组或者对象的时候，得写下面这种格式
      default: () => [],
    },
  },
  methods: {
    selecting(item) {
      this.$emit("selecting", item);
    },
  },
};
</script>

<style scoped lang="less">
@import "~@/styles/common.less";
@import "~@/styles/var.less";
.right-list-container {
  span {
    cursor: pointer;
    // font-size: 20px;
    &.selecting {
      color: #dc6a12;
    }
    &.count {
      padding-left: 8px;
      font-size: 14px;
      font-style: italic;
    }
  }
  .right-list-container {
    padding-left: 40px;
  }
}
</style>