// 显示所有文章分类
<template>
  <div class="blogCategory-container" v-loading="isLoading">
    <h2>文章分类</h2>
    <RightList :listData="typeList" @selecting="handleChangeCate" />
  </div>
</template>

<script>
// 导入关于 blog 的数据接口
import * as API from "@/api/api-blog.js";
// 导入获取远程数据的公共配置
import { getData } from "@/mixins";
// 导入右边列表组件
import RightList from "./RightList.vue";

export default {
  mixins: [getData()],
  computed: {
    // 获取分类 ID
    getCateId() {
      return +this.$route.params.categoryId || -1;
    },
    // 设置数据
    typeList() {
      // 总文章数
      const sumArticleCount = this.data.reduce((a, b) => {
        return a + b.articleCount;
      }, 0);
      const result = [
        {
          name: "全部",
          articleCount: sumArticleCount,
          id: -1,
        },
        ...this.data,
      ];
      return result.map((item) => ({
        ...item,
        isSelecting: item.id === this.getCateId,
      }));
    },
  },
  components: {
    RightList,
  },
  methods: {
    async fatchData() {
      return await API.getBlogType();
    },
    // 响应改变类别
    handleChangeCate(e) {
      const query = this.$route.query;
      // // 判断点击的类别是全部还是特定
      if (e.id === -1) {
        this.$router.push({
          name: "Blog",
          query,
        });
      } else {
        this.$router.push({
          name: "ClassifyBlog",
          query,
          params: {
            categoryId: e.id,
          },
        });
      }
    },
  },
};
</script>

<style scoped lang="less">
.blogCategory-container {
  width: 300px;
  height: 100%;
  position: relative;
  padding: 10px 0 0 20px;
  .right-list-container {
    line-height: 40px;
  }
}
</style>