// 项目页面组件
<template>
  <div class="project-container" v-loading="loading">
    <ul class="project-list">
      <li class="item" v-for="item in data" :key="item.id">
        <div class="show-img">
          <img :src="item.thumb" alt="" v-lazy="item.thumb" />
        </div>

        <div class="item-info">
          <div class="item-title">
            <h2>
              {{ item.name }}
              <a :href="item.url" target="_blank" v-if="item.url">项目链接</a>
            </h2>
            <span>{{ item.date }}</span>
          </div>
          <p v-for="(text, i) in item.description" :key="i">{{ text }}</p>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import Img from "@/assets/imgLoading.gif";
import { mapState } from "vuex";
export default {
  data() {
    return {
      Img,
    };
  },
  mounted() {
    this.$store.dispatch("project/getData");
  },
  computed: {
    ...mapState("project", ["loading", "data"]),
  },
};
</script>

<style scoped lang="less">
@import "~@/styles/var.less";
.project-container {
  min-width: 900px;
  position: relative;
  height: 100vh;
  overflow-y: scroll;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

.project-list {
  padding: 10px 20px;
}

.item {
  width: 100%;
  display: flex;
  // cursor: pointer;
  padding: 20px;
  transition: 0.3s;
  // outline: 1px solid;
  // justify-content: center;
  // box-shadow: ;
  margin-bottom: 30px;
  &:hover {
    box-shadow: -1px 1px 5px #000;
    transform: scale(1.01) translate(3px, -3px);
    color: inherit;
  }
}

.show-img {
  width: 200px;
  height: 150px;
  margin-right: 20px;
  flex: 0 0 auto;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 5px;
  }
}

.item-info {
  flex: 1 1 auto;
}

.item-title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  a,
  span {
    font-size: 14px;
    color: @gray;
  }
  span {
    margin-top: 13px;
  }
  a:hover {
    color: @primary;
  }
  & ~ p {
    color: @words;
  }
}
</style>