// 个人详情页的主内容区域
// 根据文章对象显示出文章信息
<template>
  <div class="blog-detail-container">
    <h2>{{ blog.title }}</h2>
    <p class="info">
      <a class="author" href="">刘峰</a>
      参与<span>{{ blog.scanNumber }}</span>
      <a href="#messageArea-form"
        >评论<span>{{ blog.commentNumber }}</span></a
      >
      支持<span>0</span>
      <span class="date">{{ blog.createDate }}</span>
      <RouterLink class="category" :to="`/blog/cate/${blog.category.id}`">{{
        blog.category.name
      }}</RouterLink>
    </p>
    <div class="markdown-body" v-html="blog.htmlContent"></div>
  </div>
</template>

<script>
// 引入特殊的 CSS
import "highlight.js/styles/github.css";

export default {
  props: {
    blog: {
      type: Object,
      required: true,
    },
  },
};
</script>

