// 【关于我】页面的数据仓库
import getAbout from "@/api/api-about";

export default {
    namespaced: true,
    state: {
        loading: false,
        data: null
    },
    mutations: {
        // 设置状态
        setStatus(state,payload) {
            state.loading = payload;
        },
        // 设置 banner 数据
        setData(state,payload) {
            state.data = payload;
        }
    },
    actions: {
        async getData(ctx) {
            if (ctx.state.data) {
                return;
            }
            ctx.commit('setStatus',true);
            const resp = await getAbout();
            ctx.commit('setStatus',false);
            ctx.commit('setData',resp);
        }
    }
};