// 留言板页面组件
<template>
  <div class="message-container" ref="container" v-loading="isLoading">
    <div class="message-content" v-if="!isLoading">
      <Message
        :listData="data.rows"
        title="留言列表"
        :total="data.total"
        @submit="handleSubmit"
      />
    </div>
  </div>
</template>

<script>
// 导入留言板插件
import Message from "@/components/MessageArea";
// 导入关于 blog 的数据接口
import * as API from "@/api/api-blog.js";
// 导入获取远程数据的公共配置
import { getData, scrollTop } from "@/mixins";

export default {
  mixins: [getData(), scrollTop("container")],
  components: {
    Message,
  },
  data() {
    return {
      page: 1,
      limit: 10,
    };
  },
  computed: {
    // 判断是否还有数据
    hasMoreData() {
      return this.data.total <= this.data.rows.length;
    },
  },
  mounted() {
    this.$Bus.$on("handleScroll", this.scrollLowest);
  },
  destroyed() {
    this.$Bus.$off("handleScroll", this.scrollLowest);
  },
  methods: {
    async fatchData() {
      return await API.getComment(
        this.page,
        this.limit,
        this.$route.params.categoryId
      );
    },
    async handleSubmit(data, callBack) {
      const resp = await API.submitComment({
        blogId: this.$route.params.categoryId,
        ...data,
      });
      this.data.rows.unshift(resp);
      callBack("评论成功");
    },
    // 获取更多评论数据
    async getMoreData() {
      if (this.hasMoreData) {
        return;
      }
      this.isLoading = true;
      const resp = await this.fatchData();
      this.data.rows = this.data.rows.concat(resp.rows);
      this.isLoading = false;
    },
    // 当滚动条到最低的时候触发事件
    scrollLowest(dom) {
      // console.log(123);
      if (this.isLoading) {
        return;
      }

      const range = 100;
      // 总高度：dom.scrollHeight
      // 可见高度：dom.clientHeight
      // 滚动的高度：dom.scrollTop
      const desc = dom.scrollHeight - dom.clientHeight - dom.scrollTop;
      // console.log(desc);
      if (range > desc) {
        this.getMoreData();
      }
      // this.getMoreData();
    },
  },
};
</script>

<style scoped lang="less">
.message-container {
  overflow-y: scroll;
  scroll-behavior: smooth;
  position: relative;
}
.message-content {
  width: 700px;
  height: 100vh;
  margin: 0 auto;
}
</style>