// 全局数据配置
import getSetting from "@/api/api-setting";
// 导入设置标题的方法
import {setTitle} from "@/untils";

export default {
    namespaced: true,
    state: {
        loading: false,
        data: null
    },
    mutations: {
        // 设置状态
        setSettingStatus(state,payload) {
            state.loading = payload;
        },
        // 设置 banner 数据
        setSetting(state,payload) {
            state.data = payload;
        }
    },
    actions: {
        async getData(ctx) {
            if (ctx.state.data) {
                return;
            }
            ctx.commit('setSettingStatus',true);
            const resp = await getSetting();
            ctx.commit('setSettingStatus',false);
            ctx.commit('setSetting',resp);
            if (resp.favicon) {
                // <link rel="shortcut icon" href="favicon.ico" type="image/x-icon">
                let link = document.querySelector('link[rel="shortcut icon"]');
                if (link) {
                    link.href = resp.favicon;
                }else {
                    link = document.createElement('link');
                    link.rel = 'shortcut icon';
                    link.href = resp.favicon;
                    link.type = 'image/x-icon';
                    document.querySelector('head').appendChild(link);
                }
            }
            if (resp.siteTitle) {
                setTitle.setSiteTitle(resp.siteTitle);
            }
        }
    }
};