import Vue from "vue";
// 上面写的方法这个空的 Vue 实例都有
/**
 * 事件名：handleScroll
 * 含义：主区域滚动条位置变化后触发
 * 参数：
 *  - 滚动的 dom 元素
 * 
 * 事件名：setScrollTop
 * 含义：设置主区域滚动条位置高度
 * 参数：
 *  - 需要设置的高度
 */

const App = new Vue({})
Vue.prototype.$Bus = App;

// 导出事件总线
export default App;