// 获取个人博客内容的接口
// 导入默认的配置请求
import instance from "./request";

// 获取所有博客分类
async function getBlogType() {
    const resp = await instance.get('/api/blogtype');
    return resp;
}

// 分页获取博客
async function getBlog(page = 1, limit = 10, categoryid = -1) {
    const resp = await instance.get('/api/blog', {
        params: {
            // 当前页码
            page,
            // 页容量
            limit,
            // 所属分类，-1 表示全部
            categoryid
        }
    });
    return resp;
}

// 获取单个博客
/**
 * 根据 id 获取到指定的博客
 * @param {String} id 博客的 Id
 */
async function getSingleBlog(id) {
    const resp = await instance.get(`/api/blog/${id}`);
    return resp;
}

// 提交评论
/**
 * {
	nickname: "昵称",
	content: "评论内容，纯文本",
	blogId: <id>	#评论的博客id
}
 * @param {Object} info 请求体 
 * @returns 
 */
async function submitComment(info) {
    const resp = await instance.post('/api/comment', {
        params: info
    });
    return resp;
}

// 分页获取评论
/**
 * 
 * @param {Number} page 当前页码
 * @param {Number} limit 页容量
 * @param {Number} blogid 所属文章，-1 表示不限
 * @returns 
 */
async function getComment(page = 1, limit = 10, blogid = -1) {
    const resp = await instance.get('/api/comment', {
        params: {
            page,
            limit,
            blogid
        }
    });
    return resp;
}

export {
    getBlogType,
    getBlog,
    getSingleBlog,
    submitComment,
    getComment
};