// 博客列表区域
<template>
  <ul class="blogList-container" v-loading="isLoading" ref="container">
    <li v-for="item in data.rows" :key="item.id">
      <RouterLink class="show-img" v-if="item.thumb" :to="`/blog/${item.id}`">
        <img :src="item.thumb" alt="" v-lazy="item.thumb" />
      </RouterLink>
      <div class="content-left">
        <RouterLink :to="`/blog/${item.id}`" class="content">
          <h2>{{ item.title }}</h2>
          <p class="desc">
            {{ item.description }}
          </p>
        </RouterLink>
        <p class="info">
          <a class="author" href="">刘峰</a>
          参与<span>{{ item.scanNumber }}</span> 评论<span>{{
            item.commentNumber
          }}</span>
          支持<span>0</span>
          <span class="date">{{ item.createDate }}</span>
          <RouterLink class="category" :to="`/blog/cate/${item.category.id}`">{{
            item.category.name
          }}</RouterLink>
        </p>
      </div>
    </li>
    <Page
      :total="data.total"
      @changeCurrentPage="handleChangeCurrentPage"
      :current="getRouteInfo.current"
      :limit="getRouteInfo.limit"
    />
  </ul>
</template>

<script>
// 导入关于 blog 的数据接口
import * as API from "@/api/api-blog.js";
// 导入分页组件
import Page from "@/components/Page/Page.vue";
// 导入获取远程数据的公共配置
import { getData, scrollTop } from "@/mixins";
// 导入分页布局组件
import Layout from "@/components/Layout/Layout.vue";
// 导入加载图标 loading
import { loading } from "@/instruction";

export default {
  mixins: [getData(), scrollTop("container")],
  components: {
    Page,
    Layout,
    loading,
  },
  computed: {
    getRouteInfo() {
      // 因为这里是获取别人的值，所以得用 computed
      const cate = +this.$route.params.categoryId || -1;
      const current = +this.$route.query.page || 1;
      const limit = +this.$route.query.limit || 10;
      return {
        cate,
        current,
        limit,
      };
    },
  },
  watch: {
    async $route() {
      // console.log(this.routerInfo.current);
      this.isLoading = true;
      this.$refs.container.scrollTop = 0;
      this.data = await this.fatchData();
      this.isLoading = false;
    },
  },
  methods: {
    async fatchData() {
      return await API.getBlog(
        this.getRouteInfo.current,
        this.getRouteInfo.limit,
        this.getRouteInfo.cate
      );
    },
    handleChangeCurrentPage(newPage) {
      // this.getRouteInfo.current = newPage;
      const query = {
        page: newPage,
        limit: this.getRouteInfo.limit,
      };

      // 判断当前是否有在特定的类别中
      if (this.getRouteInfo.cate === -1) {
        this.$router.push({
          name: "Blog",
          query,
        });
      } else {
        this.$router.push({
          name: "ClassifyBlog",
          query,
          params: {
            categoryId: this.getRouteInfo.cate,
          },
        });
      }
    },
  },
};
</script>

<style scoped lang="less">
.blogList-container {
  min-width: 680px;
  height: 100vh;
  padding: 0px 10px 20px;
  overflow-y: scroll;
  scroll-behavior: smooth;
  .page-container {
    margin-top: 30px;
  }
}

.blogList-container li {
  display: flex;
  border-bottom: 1px solid #ccc;
  padding: 20px 0;
  &:hover {
    background: #f9f9f9;
  }
}

.blogList-container a:hover {
  color: inherit;
}

.show-img {
  margin-top: 10px;
  // width: 300px;
  // height: 250px;
  img {
    display: block;
    object-fit: cover;
    width: 160px;
    height: 120px;
    border-radius: 5px;
  }
}

.content-left {
  width: 100%;
  padding: 0 10px;
  h2 {
    height: 40px;
    line-height: 40px;
  }
  .desc {
    height: 72px;
    line-height: 24px;
    overflow: hidden;
    text-overflow: ellipsis;
    color: #707070;
    //   background: #ccc;
    margin-bottom: 10px;
  }
}
.info {
  height: 21px;
  line-height: 21px;
  .author {
    color: #f40;
    margin: 0 10px;
    &:hover {
      color: #f40;
    }
  }
  span {
    color: rgb(167, 0, 0);
    margin: 0 10px 0 3px;
  }
  .category {
    font-weight: bold;
  }
  .date {
    float: right;
    font-style: italic;
    color: #ccc;
  }
}
</style>