// 设置浏览器标题
let routerTitle = '',   // 路由标题
    siteTitle = '';     // 网站标题

function setTitle() {
    // 如果路由标题和网站标题都没有则显示 'Loading...'
    if (!routerTitle && !siteTitle) {
        document.title = 'Loading...';
    }
    // 如果有路由标题，但网站标题没有，则显示路由标题
    else if(routerTitle && !siteTitle) {
        document.title = routerTitle;
    }
    // 如果有网站标题，但路由标题没有，则显示网站标题
    else if(!routerTitle && siteTitle) {
        document.title = siteTitle;
    }
    // 两个标题都有
    else {
        document.title = `${routerTitle}-${siteTitle}`;
    }
}

export default {
    // 设置路由标题
    setRouterTitle(title) {
        routerTitle = title;
        setTitle();
    },
    // 设置网站标题
    setSiteTitle(title) {
        siteTitle = title;
        setTitle();
    },
}