// 路由模块
import Vue from "vue";
import Router from "vue-router";
import routes from "./routes";
// 导入设置标题的方法
import {setTitle} from "@/untils";

// 判断是否是通过传统代码来引入 Vue
if (!window.VueRouter) {
    Vue.use(Router);
}

const router = new Router({
    // 路由规则
    routes,
    // 路由模式
    mode: "hash"
});

router.afterEach((to) => {
    if (to.meta.title) {
        setTitle.setRouterTitle(to.meta.title);
    }   
})

export default router;