// 单页博客内容
<template>
  <div class="detail-blog-container">
    <layout>
      <div class="detail-blog-left" v-loading="isLoading" ref="container">
        <BlogDetail v-if="data" :blog="data" />
        <BlogComment v-if="!isLoading" />
      </div>

      <template #right>
        <div class="detail-blog-right" v-loading="isLoading">
          <h2>目录</h2>
          <BlogDetailMenu :toc="data.toc" v-if="data" />
          <!-- <BlogDetail v-if="data" /> -->
        </div>
      </template>
    </layout>
  </div>
</template>

<script>
// 导入关于 blog 的数据接口
import * as API from "@/api/api-blog.js";
// 导入获取远程数据的公共配置
import { getData, scrollTop } from "@/mixins";
// 导入插槽 Layout 用来分栏布局
import Layout from "@/components/Layout/Layout.vue";
// 导入个人详情页的主内容区域
import BlogDetail from "./components/BlogDetail.vue";
// 导入个人详情页的右边栏区域
import BlogDetailMenu from "./components/BlogDetailMenu.vue";
// 导入个人详情页的评论区域
import BlogComment from "./components/BlogComment.vue";
// 导入设置标题的方法
import { setTitle } from "@/untils";

export default {
  mixins: [getData(null), scrollTop("container")],
  components: {
    Layout,
    BlogDetail,
    BlogDetailMenu,
    BlogComment,
  },
  updated() {
    const hash = location.hash;
    // console.log(hash);
    location.hash = "";
    setTimeout(() => {
      location.hash = hash;
    }, 50);
  },
  methods: {
    async fatchData() {
      const resp = await API.getSingleBlog(this.$route.params.categoryId);
      // resp = null;
      if (!resp) {
        // console.log("null");
        this.$router.push("/404");
        return;
      }
      setTitle.setRouterTitle(resp.title);
      return resp;
    },
  },
};
</script>

<style scoped lang="less">
.detail-blog-left,
.detail-blog-right {
  position: relative;
  height: 100vh;
}

.detail-blog-left {
  overflow-x: hidden;
  overflow-y: scroll;
  scroll-behavior: smooth;
}

.detail-blog-right {
  width: 300px;
  padding: 15px 30px;
  line-height: 30px;
}
</style>