// 回到页面顶部插件
<template>
  <div class="toTop-container" v-show="show" @click="setScroll">top</div>
</template>

<script>
export default {
  data() {
    return {
      show: false,
    };
  },
  mounted() {
    this.$Bus.$on("handleScroll", this.showTop);
  },
  destroyed() {
    this.$Bus.$off("handleScroll", this.showTop);
  },
  methods: {
    showTop(dom) {
      const range = 300;
      const offset = dom.scrollTop;
      if (offset > range) {
        this.show = true;
      } else {
        this.show = false;
      }
    },
    setScroll() {
      this.$Bus.$emit("setScroll", 0);
    },
  },
};
</script>

<style scoped lang="less">
@import "~@/styles/var.less";
.toTop-container {
  width: 50px;
  height: 50px;
  position: fixed;
  right: 50px;
  bottom: 50px;
  z-index: 9999;
  background: @primary;
  border-radius: 50%;
  color: #fff;
  text-align: center;
  line-height: 50px;
  cursor: pointer;
}
</style>