// 个人详情页的右边栏区域
<template>
  <div class="blog-detail-menu-container">
    <RightList :listData="tocWithActive" @selecting="changeHash" />
  </div>
</template>

<script>
// 导入右边列表组件
import RightList from "./RightList.vue";
// 导入函数防抖组件
import { debounce } from "@/untils";

export default {
  components: {
    RightList,
  },
  props: {
    toc: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      // 目前激活的锚点
      activeAnchor: "",
    };
  },
  computed: {
    // 根据 toc 属性以及 activeAnchor 得到带有 isSelecting 属性的 toc 数组
    tocWithActive() {
      // 给每一个 toc 里面的对象增加一个 isSelecting 属性，至于这个属性的值是 true 还是 false ，得看看它是不是目前激活的锚点
      const newToc = (toc = []) => {
        return toc.map((t) => {
          return {
            ...t,
            isSelecting: t.anchor === this.activeAnchor,
            // 里面每一个下一个层次的标题列表也进行同样的操作
            children: newToc(t.children),
          };
        });
      };
      return newToc(this.toc);
      // const getNewToc = (toc = []) => {
      //   return {
      //     ...toc,
      //     isSelecting: toc.anchor === this.activeAnchor,
      //     children: getNewToc(toc.children)
      //   }
      // };
      // return getNewToc(this.toc);
    },
    // 获取需要知道位置的 DOM 元素
    doms() {
      const allDoms = [];
      const getDoms = (toc) => {
        // 把 toc 里面每一项都放到 doms 数组里面
        for (const t of toc) {
          allDoms.push(document.getElementById(t.anchor));
          // 判断有没有第二层级的元素，如果有也放到 doms 数组中
          if (t.children && t.children.length) {
            getDoms(t.children);
          }
        }
      };
      getDoms(this.toc);
      return allDoms;
    },
  },
  mounted() {
    this.deboun = debounce(this.setActiveAnchor, 500);
    this.$Bus.$on("handleScroll", this.deboun);
  },
  destroyed() {
    this.$Bus.$off("handleScroll", this.deboun);
  },
  methods: {
    changeHash(el) {
      // console.log(el);
      // console.log(location);
      location.hash = el.anchor;
    },
    setActiveAnchor() {
      this.activeAnchor = "";
      // 范围值
      const range = 200;
      for (const dom of this.doms) {
        // console.log(dom);
        // 如果 dom 元素不存在就退出
        if (!dom) {
          return;
        }
        const clientTop = dom.getBoundingClientRect().top;
        // console.log(clientTop);
        if (clientTop < range && clientTop >= 0) {
          // 说明当前标题距离顶部在 200px 以内
          this.activeAnchor = dom.id;
          return;
        } else if (clientTop > range) {
          // 说明当前标题距离顶部在 200px 以上
          return;
        } else {
          this.activeAnchor = dom.id;
        }
      }
    },
  },
};
</script>

