// 消息区域组件，可用于[评论区]和 [留言板] 模块
<template>
  <div class="messageArea-container">
    <form
      class="messageArea-form"
      @submit.prevent="submit"
      ref="form"
      id="messageArea-form"
    >
      <!-- 用户昵称 -->
      <div class="nickname">
        <input
          type="text"
          placeholder="用户昵称"
          maxlength="10"
          v-model="formData.nickName"
        />
        <span>{{ formData.nickName.length }}/10</span>
        <div class="errorText">{{ error.nickName }}</div>
      </div>

      <!-- 内容 -->
      <div class="content">
        <textarea
          maxlength="100"
          placeholder="输入内容"
          v-model="formData.content"
        >
        </textarea>
        <span>{{ formData.content.length }}/100</span>
        <div class="errorText">{{ error.content }}</div>
      </div>
      <!-- 提交按钮 -->
      <button type="submit" :disabled="isSubmiting">
        {{ isSubmiting ? "提交中..." : "提交" }}
      </button>
    </form>
    <h3>{{ title }}（{{ total }}）</h3>
    <ul class="comment-list" v-if="total !== 0">
      <li v-for="item in listData" :key="item.id">
        <!-- 头像 -->
        <img :src="item.avatar" alt="" class="portrait" />
        <!-- 评论信息 -->
        <div class="commonet-info">
          <p class="commonet-user">
            <!-- 昵称 -->
            <a href="">{{ item.nickname }}</a>
            <!-- 评论时间 -->
            <span>{{ item.createDate }}</span>
          </p>
          <p class="commonet-content">
            {{ item.content }}
          </p>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isSubmiting: false,
      formData: {
        nickName: "",
        content: "",
      },
      error: {
        nickName: "",
        content: "",
      },
    };
  },
  props: {
    // 数据列表
    listData: {
      type: Array,
      defaultd: () => [],
    },
    // 列表标题
    title: {
      type: String,
      defaultd: "",
    },
    // 评论数量
    total: {
      type: Number,
      defaultd: 0,
    },
  },
  methods: {
    submit() {
      this.error.nickName = this.formData.nickName ? "" : "昵称不能为空！";
      this.error.content = this.formData.content ? "" : "内容不能为空！";

      if (this.error.nickName || this.error.content) {
        return;
      }

      this.isSubmiting = true;
      this.$emit(
        "submit",
        {
          nickname: this.formData.nickName,
          content: this.formData.content,
        },
        (content) => {
          this.popover({
            content,
            type: "success",
            time: 1000,
            elem: this.$refs.form,
          });
          this.isSubmiting = false;
          this.error.nickName = "";
          this.error.content = "";
          this.formData.nickName = "";
          this.formData.content = "";
        }
      );
    },
  },
};
</script>

<style scoped lang="less">
@import "~@/styles/common.less";
@import "~@/styles/var.less";
.messageArea-container {
  padding: 10px;
  h3 {
    font-weight: normal;
    margin: 15px 0;
  }
}
.messageArea-form {
  span {
    color: #ccc;
    font-size: 12px;
  }
  button {
    width: 90px;
    height: 30px;
    cursor: pointer;
    outline: none;
    border: none;
    color: #fff;
    background: #3295d9;
    border-radius: 5px;
    transition: 0.3s;
    margin-left: 10px;
    &:hover {
      background: @primary;
    }
    &:disabled {
      cursor: not-allowed;
    }
  }
  .errorText {
    color: red;
  }
}

.nickname {
  height: 30px;
  padding: 0 10px;
  width: 30%;
  position: relative;
  input {
    width: 100%;
    height: 100%;
    outline: none;
    border: none;
    font-size: 12px;
    border: 1px dashed #ccc;
    border-radius: 5px;
    padding: 10px;
    &:focus {
      border-color: @primary;
    }
  }
  span {
    position: absolute;
    right: 20px;
    bottom: 50%;
    transform: translateY(50%);
  }
}

.content {
  margin: 35px 0;
  position: relative;
  width: 60%;
  padding: 0px 10px;
  textarea {
    width: 100%;
    height: 80px;
    resize: none;
    outline: none;
    border: 1px dashed #ccc;
    border-radius: 5px;
    padding: 10px 5px;
    &:focus {
      border-color: @primary;
    }
  }
  span {
    position: absolute;
    right: 15px;
    bottom: 5px;
  }
}

.comment-list {
  li {
    display: flex;
    border-bottom: 1px solid #ccc;
    padding: 20px 0;
  }
}

.portrait {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-right: 20px;
}

.commonet-info {
  flex: 1 1 auto;
}

.commonet-user {
  display: flex;
  justify-content: space-between;
  margin-bottom: 8px;
  a {
    color: @success;
    font-size: 16px;
  }
  span {
    font-size: 12px;
    color: @lightWords;
  }
}
</style>